@font-face{
  font-family: 'MyFont';
  src: url('./selawkl.ttf') format('truetype');
}

.header{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f7f6fb;

  width: 100%;
  font-family: 'MyFont';
  font-size: 2.5vw;
}

#logo{
  width: 8%;
  margin-left: 1%;
  margin-right: 1%;
  /* border: solid thin */
}

.patreon{
  font-family: 'MyFont';
  color: black;
  width: max-content;
  border: solid thin;
  border-radius: 5px;
  background: #f7f6fb;
}

.patreon:hover{
  color: white;
  background-color: black;
}

p{
  margin-left: 7.5%;
  margin-right: 7.5%;
}
